import React, { Component } from 'react'
import '../Typography/Typography.css'

import Period from '../Period/Period'
import PeriodSelector from '../PeriodSelector/PeriodSelector.js'
import PeriodCalendar from '../PeriodCalendar/PeriodCalendar.js'
import Calendar from '../Calendar/Calendar.js'

import axios from 'axios'
import Cookies from 'js-cookie'

import { PieChart, Pie, Legend, Tooltip } from 'recharts'

import moment from 'moment'

function cleanData(stats) {
    const colors = {
        'с 9:00 до 13:00': '#02EF6F',
        'c 13:00 до 14:00': '#00AB54',
        'c 14:00 до 18:00': '#007537',
        'c 18:00 до 08:00': '#084D28'
    }

    return (stats.map(row => {
        return {
            name: row.name,
            value: parseInt(row.value),
            fill: colors[row.name]
        }
    })).filter(({ value }) => value)
}

function getCurrentWeek() {
    return {
        start: moment().startOf('week').format('YYYY-MM-DD'),
        end: moment().endOf('week').format('YYYY-MM-DD'),
        intervalType: 'days'
    }
}

const fakeData = [
    {
        "name": "с 9:00 до 13:00",
        "value": 45,
        "fill": "#02EF6F"
    },
    {
        "name": "c 13:00 до 14:00",
        "value": 7,
        "fill": "#00AB54"
    },
    {
        "name": "c 14:00 до 18:00",
        "value": 31,
        "fill": "#007537"
    },
    {
        "name": "c 18:00 до 08:00",
        "value": 0,
        "fill": "#084D28"
    }
]


const settings = { top: 30, right: 0, left: 0, bottom: 0 };

class TheChart extends React.Component {
    render() {
        return (
            <PieChart width={800} height={425}>
                <Pie data={this.props.data}
                    outerRadius={140} fill="#FF0000" label />
                <Tooltip />
            </PieChart>
        );
    }
}

export default class extends Component {
    state = { periodMode: 'interval', interval: getCurrentWeek() }

    componentDidMount() {
        this.fetch()
    }

    onPeriodChange({ mode }) {
        this.setState({ periodMode: mode, interval: false })
    }

    onInterval({ start, end, intervalType }) {
        console.log('onInterval', { start, end, intervalType })
        this.setState(
            { interval: { start, end, intervalType } },
            () => this.fetch()
        )
    }

    fetch() {
        this.setState({ stats: false }, () => {
            // let url = `http://papi.mindsales-crm.com/stats/calls?token=${Cookies.get('msid')}`
            let url = `http://localhost:5002/stats/incoming/time?token=${Cookies.get('msid')}`

            const { start, end, intervalType } = this.state.interval
            if (start) url = `${url}&start=${start}`
            if (end) url = `${url}&end=${end}`
            if (intervalType) url = `${url}&type=${intervalType}`

            axios.get(url)
                .then(({ data: { items } }) => {
                    this.setState({ stats: cleanData(items) })
                })
                .catch(console.log);
        })
    }


    render() {
        window.scrollTo(0, 0)

        const { periodMode, interval, stats } = this.state
        const graph = stats ? cleanData(stats) : false

        return (
            <section className="bContent b">
                <header className="bContent__header">Время входящих</header>
                <PeriodSelector mode={periodMode}
                    onChange={this.onPeriodChange.bind(this)} />

                {periodMode === 'interval' && <Period interval={interval} onInterval={this.onInterval.bind(this)} />}

                {periodMode === 'day' && <Calendar />}

                {graph.length > 0 && <TheChart data={graph} />}

                {graph.length === 0 && <p style={{ padding: '2rem 0', textAlign: 'center' }}>Звонков не было 🙈</p>}
                {graph === false && <p style={{ padding: '2rem 0', textAlign: 'center' }}>Загружаем статистику... 👀</p>}
            </section>
        );
    }
}


